// const Buffer = require('buffer/').Buffer
const jwtDecode = require('jwt-decode')
export default {
  // Paylaod token
  getPaylaod () {
    let auth = this.getToken()
    let payload = {}
    let isAuth = !!auth
    if (isAuth) {
      payload = jwtDecode(auth)
    }
    return payload
  },
  // 
  getUserName () {
    let auth = this.getToken()
    let payload = {}
    let isAuth = !!auth
    if (isAuth) {
      payload = jwtDecode(auth)
    }
    return payload.user_name
  },
  ReadRole () {
    let auth = this.getToken()
    let payload = {}
    let isAuth = !!auth
    if (isAuth) {
      payload = jwtDecode(auth)
    }
    let ispayload = !!payload
    if (ispayload) {
      return payload.role
    }
    return null
  },
  // head token 
  getToken () {
    // sessionStorage
    var token = sessionStorage.getItem('Authorization')
    return token
  },
  // token 
  setToken (token) {
    // sessionStorage
    // dev token
    sessionStorage.setItem(
      'Authorization', 'Bearer ' + token
    )
  },
  // token 
  setPatientInfo (Info) {
    // sessionStorage
    // dev token
    sessionStorage.setItem(
      'pat_info', encodeURIComponent(JSON.stringify(Info))
    )
  },
  // head token 
  getPatientInfo () {
    // sessionStorage
    var patInfo = JSON.parse(decodeURIComponent(sessionStorage.getItem('pat_info')))
    return patInfo
  },
  PatientInfoExist () {
    var data = !!sessionStorage.getItem('pat_info')
    return data
  },
  setPatList (Info) {
    // sessionStorage
    // dev token
    sessionStorage.setItem(
      'PatList', encodeURIComponent(JSON.stringify(Info))
    )
  },
  getPatList () {
    // sessionStorage
    var PatList = JSON.parse(decodeURIComponent(sessionStorage.getItem('PatList')))
    return PatList
  },
  PatListExist () {
    var data = !!sessionStorage.getItem('PatList')
    return data
  },
  // token 
  TokenExist () {
    var token = !!sessionStorage.getItem('Authorization')
    return token
  },
  // clear token
  clearToken () {
    // sessionStorage
    // dev token
    sessionStorage.removeItem(
      'Authorization'
    )
    sessionStorage.removeItem(
      'pat_info'
    )
    sessionStorage.removeItem(
      'isGuide'
    )
  },
  isGuide () {
    var isGuide = !!sessionStorage.getItem('isGuide')
    return isGuide
  },
  getTypeMode () {
    return sessionStorage.getItem('type_mode')
  }
}



