<style scoped>
</style>
<template>
  <div class="m-2">
    <a-row>
      <a-col :span="24">
        
        <span class="m-2 px-4 bg-white">{{patListLength}}</span>
      </a-col>
    </a-row>
    <a-row>
      <a-card title="">
        <a-button-group slot="extra">
          <a-button type="primary" @click="AutoCreatePatData"></a-button>
        </a-button-group>
        <div v-if="loading">
          <a-skeleton active />
          <a-skeleton active />
        </div>
        <div v-else>
          <a-form layout="inline">
            <div class="absolute right-0">
              <!-- <img class="mr-16" src="@/assets/TMU.png" alt="TMU" />-->
            </div>
            <a-form-item
              style="display:block;"
              :labelCol="{span: 3}"
              required
              label=""
              :validate-status="!!model.char_no ? '' : 'error'"
              :help="!!model.char_no ? ' ':''"
            >
              <a-input v-model="model.char_no" placeholder="" />
            </a-form-item>
            <a-form-item
              style="display:block;"
              :labelCol="{span: 3}"
              required
              label=""
              :validate-status="!!model.pat_name ? '' : 'error'"
              :help="!!model.pat_name ? ' ':''"
            >
              <a-input v-model="model.pat_name" placeholder="" />
            </a-form-item>
            <a-form-item
              style="display:block;"
              :labelCol="{span: 3}"
              required
              label=""
              :validate-status="!!model.sex ? '' : 'error'"
              :help="!!model.sex ? ' ':''"
            >
              <a-radio-group v-model="model.sex" buttonStyle="solid">
                <a-radio-button value="1"></a-radio-button>
                <a-radio-button value="0"></a-radio-button>
              </a-radio-group>
            </a-form-item>
            <a-form-item
              style="display:block;"
              :labelCol="{span: 3}"
              required
              label=""
              :validate-status="!!model.ageType ? '' : 'error'"
              :help="!!model.ageType ? ' ':''"
            >
              <a-radio-group v-model="model.ageType" buttonStyle="solid">
                <a-radio-button value="0">0-14 ()</a-radio-button>
                <a-radio-button value="1">15-64 ()</a-radio-button>
                <a-radio-button value="2">65 ()</a-radio-button>
              </a-radio-group>
            </a-form-item>
            <a-form-item
              style="display:block;"
              :labelCol="{span: 3}"
              required
              label=""
              :validate-status="!!model.injury_classification ? '' : 'error'"
              :help="!!model.injury_classification ? ' ':''"
            >
              <a-radio-group v-model="model.injury_classification" buttonStyle="solid">
                <a-radio-button class="text-red-500 bg-red-200" value="Severe">Severe</a-radio-button>
                <a-radio-button class="text-blue-500 bg-blue-200" value="Moderate">Moderate</a-radio-button>
                <a-radio-button class="text-green-500 bg-green-200" value="Mild">Mild</a-radio-button>
              </a-radio-group>
              <span
                class="m-2"
                :class="{'text-red-500' : model.injury_classification === 'Severe' ,
              'text-blue-500' : model.injury_classification === 'Moderate' ,
              'text-green-500' : model.injury_classification === 'Mild'
               }"
              >{{model.injury_classification}}</span>
            </a-form-item>
            <a-form-item
              style="display:block;"
              :labelCol="{span: 3}"
              required
              label=""
              :validate-status="!!model.nation_type ? '' : 'error'"
              :help="!!model.nation_type ? ' ':''"
            >
              <a-radio-group v-model="model.nation_type" buttonStyle="solid">
                <a-radio-button value="1"></a-radio-button>
                <a-radio-button value="2"></a-radio-button>
              </a-radio-group>
            </a-form-item>
            <a-form-item style="display:block;" :labelCol="{span: 3}" label="ID">
              <a-input v-model="model.amb_id" placeholder="ID" />
            </a-form-item>
            <a-form-item
              style="display:block;"
              required
              :labelCol="{span: 3}"
              label=""
              :validate-status="!!model.start_date ? '' : 'error'"
              :help="!!model.start_date ? ' ':''"
            >
              <a-date-picker
                :allowClear="false"
                format="YYYY-MM-DD HH:mm:ss"
                :value="moment(model.start_date, 'YYYY-MM-DD  HH:mm:ss')"
                :showTime="{ format:  'HH:mm:ss'  }"
                @change="(date, dateString) => SetModelDate(date, dateString, 'start_date')"
              />
            </a-form-item>
          </a-form>
        </div>

        <Alert class="m-2" ref="alert"></Alert>
        <a-row class="text-center">
          <a-button type="primary" @click="clickAdddPat"></a-button>
        </a-row>
      </a-card>
    </a-row>
  </div>
</template>
<script>
import Mixin from '@/mixin'
export default {
  mixins: [Mixin],
  data () {
    return {
      loading: false,
      selectPat: '',
      hospEvacuationVal: 0,
      totalVal: 0,
      model: this.SetNewModel()
    }
  },
  computed: {
    title () {
      return document.title
    }
  },
  methods: {
    SetModelDate (date, dateString, pId) {
      this.model[pId] = dateString
    },
    // 
    clickAdddPat () {
      this.$store.commit('SpinLoading', true)
      if (document.getElementsByClassName('has-error').length > 0) {
        this.$notification.error({
          message: '!'
        })
        this.$refs.alert.show('!')
      } else {
        this.$store.commit('SetPatList', this.model)
        this.model = this.SetNewModel()
        this.$refs.alert.showInfo('!')
      }
      this.$store.commit('SpinLoading', false)
    },
    // 
    AutoCreatePatData () {
      let tempModel = this.SetNewModel()
      tempModel.char_no = this.$moment().format('YYYYMMDDHHmmss')
      tempModel.pat_name = this.$moment().format('mmss') + ''
      this.model = tempModel
      this.$refs.alert.showInfo('!')
    }
  }
}
</script>



